.category {
  &__photos {
    @include pageGrid;
    margin-bottom: 16px;
  }

  &__link {
    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: span 6;
    }
  }

  &__photo {
    width: 100%;
    @include hover(transform, scale(1.1));
  }

  &__title {
    margin: 0;
    font-size: 18px;
    font-weight: 700;

    @include onTablet {
      font-size: 24px;
    }
  }
}
